}
@font-face {
  font-family: 'VT323';
  font-style: normal;
  font-weight: 400;
  src: local('VT323 Regular'), local('VT323-Regular'), url("fonts/VT323-latin.woff2") format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2212, U+2215;
}
.VT323,
body,
.Code .Editor textarea,
.Code .Output pre {
  font-family: 'VT323', monospace;
}
html {
  background: #08141e;
}
body {
  margin: 0;
  padding: 0;
  color: #f6d6bd;
  font-size: 1.25rem;
}
h1 {
  font-size: 1.5rem;
  text-align: center;
}
a {
  color: #997577;
}
.App {
  width: 100vw;
}
nav {
  display: flex;
  justify-content: space-between;
}
nav a {
  text-transform: uppercase;
}
nav ul {
  padding: 0;
  margin: 0;
  display: flex;
  list-style: none;
}
nav ul li {
  padding-right: 0.5em;
}
.Home .Shelf {
  border: solid #0f2a3f 2px;
  grid-column: 1/3;
}
.Covers button {
  margin-right: 0.5em;
}
.Covers span {
  padding-right: 0.5em;
}
.Covers span.time-ago {
  color: #4e495f;
}
.Code {
  display: grid;
  grid-template-columns: 45% 45%;
  grid-column-gap: 10%;
  grid-row-gap: 2em;
}
.Code > h1 {
  grid-column: 1/3;
}
.Code .Editor {
  border: solid #0f2a3f 2px;
}
.Code .Editor textarea {
  display: block;
  width: 100%;
  font-size: 1.25rem;
  background: #0f2a3f;
  color: #f6d6bd;
  border: solid #c3a38a 1px;
}
.Code .Output {
  border: solid #0f2a3f 2px;
}
.Code .Output pre {
  display: block;
  width: 100%;
  font-size: 1.25rem;
  color: #f6d6bd;
}
.Code .Shelf {
  border: solid #0f2a3f 2px;
  grid-column: 1/3;
}
